import uniListItem from '@/components/uni-ui/uni-list-item/uni-list-item.vue';
import otherLogin from '@/components/common/other-login.vue';
import { mapState } from 'vuex'
export default {
	components: {
		uniListItem,
		otherLogin
	},
	data() {
		return {
			myData:[{
				name:"帖子",
				num:0
			},{
				name:"动态",
				num:0
			},{
				name:"评论",
				num:0
			},{
				name:"粉丝",
				num:0
			}]
		}
	},
	onNavigationBarButtonTap() {
		uni.navigateTo({
			url: '../user-set/user-set'
		});
	},
	computed: {
		...mapState({
			loginStatus:state=>state.loginStatus,
			user:state=>state.user
		}),
		// 用户头像
		avatar(){
			return this.user.userpic ? this.user.userpic : '/static/default.jpg'
		}
	},
	onShow() {
		if(this.loginStatus){
			this.getCounts()
		}
	},
	watch:{
		loginStatus(newValue,oilValue){
			if(newValue){
				this.getCounts()
			}else{
				this.myData.forEach(item=>{
					item.num = 0
				})
			}
		}
	},
	methods: {
		//获取用户相关数据
		getCounts(){
			this.$H.get('/user/getcounts/' + this.user.id,{},{
				token:true,
				notoast:true
			}).then(res=>{
				//console.log(res)
				this.myData[0].num = res.post_count
				this.myData[1].num = res.today_posts_count
				this.myData[2].num = res.comments_count
				this.myData[3].num = res.withfen_count
			})
		},
		// 打开登录页
		openLogin(){
			uni.navigateTo({
				url: '../login/login',
			});
		},
		openHistory(){
			uni.navigateTo({
				url:'../history/history'
			})
		}
	}
}
